.AboutPage {
  display: flex;
  flex-direction: column;
}


.about-body {
    background-color: #FFF;
    display: flex;
    flex-direction: row;
    flex: 1;
  }

  .about-right {
    font-family: 'JetBrains Mono'; 
    width: 50%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

  }

  .about-left {
    background-color: #C4CBEC;
    width: 50%;
  }

  .about-content {
    font-size: 1.3vw;
    color: #626683;
    margin: 0;
    font-family: 'JetBrains Mono';
  }

  .about-text{
    font-family:"Abel";
    color: #394060;
    font-size:3vw;
    padding-left:10%;
    padding-right:10%;
    padding-top:5%;
  }

  .image {
    object-fit: contain;
    width:93%;
    padding-top: 3%;
    padding-bottom: 3%;
  }